export interface CopyBookSearchInterface {
    level: string
    isRowOne: string
    isCustom: string
}


export const get100RandomWord = (searchCondition: CopyBookSearchInterface) => {
    const wordTemp = deepClone(wordBank[parseInt(searchCondition.level)])
    const result: Array<Array<string>> = new Array<Array<string>>()
    console.log(searchCondition.isRowOne);
    
    if (searchCondition.isRowOne == "0") {
        for (let i = 0; i < 10; i++) {
            const temp = new Array<string>();
            for (let j = 0; j < 10; j++) {

                const index = random(0, wordTemp.length - 1)

                temp.push(wordTemp[index])
                wordTemp.splice(index, 1)
            }
            result.push(temp)
        }
    }
    else {
        for (let i = 0; i < 10; i++) {
            const index = random(0, wordTemp.length - 1)
            const temp = new Array<string>();
            for (let j = 0; j < 10; j++) {
                temp.push(wordTemp[index])
            }
            wordTemp.splice(index, 1)
            result.push(temp)
        }
    }


    return result
}

export const get100RandomWordWithoutBank = (isRowOne:string)=>{
    const result: Array<Array<string>> = new Array<Array<string>>()
    if (isRowOne == "0") {
        for (let i = 0; i < 10; i++) {
            const temp = new Array<string>();
            for (let j = 0; j < 10; j++) {
                const str = '\\u' + (Math.round(Math.random() * 20901) + 19968).toString(16)
                temp.push(unescape(str.replace(/\\u/g, "%u")))
            }
    
            result.push(temp)
        }
    }
    else {
        for (let i = 0; i < 10; i++) {
            const temp = new Array<string>();
            const str = '\\u' + (Math.round(Math.random() * 20901) + 19968).toString(16)
            for (let j = 0; j < 10; j++) {            
                temp.push(unescape(str.replace(/\\u/g, "%u")))
            }
            
            result.push(temp)
        }
    }
    return result
}

