
import { defineComponent, reactive,ref } from 'vue'
import ZiTieBase from '@/components/base/ZiTieBase.vue';

export default defineComponent({
    components: { ZiTieBase },
    setup() {

const  print=ref({
        id: 'printArea',//idid
        popTitle: '', // 
        extraHead: '', // head
        preview: false, // false
        previewTitle: '', // 
        previewPrintBtnLabel: '', // 
        zIndex: 20002, // z-index20002
        previewBeforeOpenCallback() { console.log(''); }, // callback
        previewOpenCallback() { console.log('') }, // callback
        beforeOpenCallback() { console.log('') }, // callback
        openCallback() { console.log('') }, // callback
        closeCallback() { console.log('') }, // callback(or)
        clickMounted() { console.log('v-print') },
 
      })
        const data = reactive({
            level: "0",
            isRowOne: "1",
            isCustom: "false",
            key:0
        })
        const freshKey = ()=>  {
            data.isRowOne
            data.key++
        }

        return {
            data,freshKey,print
        }
    }
})
