import { random } from './random'
import { divisors} from './divisors'
// 
export interface ChickenRabbitQuestionData {
    chickenNum: number
    rabbitNum: number
    headTotal: number
    footTotal: number
    question: string
}

export const chickenRabbitQuestionGenerator = () => {
    const result: ChickenRabbitQuestionData = {
        chickenNum: 0,
        rabbitNum: 0,
        headTotal: 0,
        footTotal: 0,
        question: ""
    }
    result.chickenNum = random(1, 50)
    result.rabbitNum = random(1, 50)
    result.headTotal = result.chickenNum + result.rabbitNum
    result.footTotal = result.chickenNum * 2 + result.rabbitNum * 4
    result.question = "" + result.headTotal + "" + result.footTotal + ""
    return result
}

//
export interface UnificationQuestionData {
    single: number
    numberKnown: number
    totalKnown: number
    numberNew: number
    totalNew: number
    problem: string
    answer: string
    type: string
    prolemTemplate: string
    answerTemplate: string
    numberUnit: string
    totalUnit: string
}
interface Problem {
    problem: string
    answer: string
    type: string
    magnitude: number
    numberUnit: string
    totalUnit: string
}
const UnificationProblems: Array<Problem> = [
    {
        'problem': "numberKnowntotalKnown,,numberNew",
        'answer': "numberNewtotalNew",
        'type': '1',
        'numberUnit': '',
        'totalUnit': '',
        'magnitude': 300
    },
    {
        'problem': "numberKnowntotalKnowntotalNew",
        'answer': "totalNewnumberNew",
        'type': '2',
        'numberUnit': '',
        'totalUnit': '',
        'magnitude': 100
    },
    {
        'problem': "numberKnowntotalKnownnumberNew",
        'answer': "totalNew",
        'type': '1',
        'numberUnit': '',
        'totalUnit': '',
        'magnitude': 500
    },
    {
        'problem': "numberKnowntotalKnownnumberNew",
        'answer': "numberNewtotalNew",
        'type': '1',
        'numberUnit': '',
        'totalUnit': '',
        'magnitude': 10
    },
    {
        'problem': "numberKnowntotalKnowntotalNew",
        'answer': "totalNewnumberNew",
        'type': '2',
        'numberUnit': '',
        'totalUnit': '',
        'magnitude': 50
    }

]
export const UnificationQuestionGenerator = () => {
    const result: UnificationQuestionData = {
        single: 0,
        numberKnown: 0,
        totalKnown: 0,
        numberNew: 0,
        totalNew: 0,
        problem: "",
        answer: "",
        type: "",
        prolemTemplate: "",
        answerTemplate: "",
        numberUnit: "",
        totalUnit: ""
    }
    const index = random(0, UnificationProblems.length - 1)
    result.prolemTemplate = UnificationProblems[index].problem
    result.answerTemplate = UnificationProblems[index].answer
    result.numberUnit = UnificationProblems[index].numberUnit
    result.totalUnit = UnificationProblems[index].totalUnit
    result.type = UnificationProblems[index].type
    result.single = random(Math.ceil(UnificationProblems[index].magnitude / 5), UnificationProblems[index].magnitude)
    result.numberKnown = random(2, 10)
    result.totalKnown = result.single * result.numberKnown
    result.numberNew = random(11, 20)
    result.totalNew = result.single * result.numberNew




    result.problem = result.prolemTemplate.replace('numberKnown', String(result.numberKnown))
    result.problem = result.problem.replace('totalKnown', String(result.totalKnown))
    result.problem = result.problem.replace('numberNew', String(result.numberNew))
    result.problem = result.problem.replace('totalNew', String(result.totalNew))

    result.answer = result.answerTemplate.replace("numberNew", String(result.numberNew))
    result.answer = result.answer.replace('totalNew', String(result.totalNew))

    return result
}

//
const ToSumUpProblems: Array<Problem> = [
    {
        'problem': "unitKnownnumberKnownunitNew",
        'answer': "unitNewnumberNew",
        'type': '1',
        'numberUnit': '',
        'totalUnit': '',
        'magnitude': 200
    }

]
interface ToSumUpProblemData {
    total: number
    unitKnown: number
    numberKnown: number
    unitNew: number
    numberNew: number
    problem: string
    answer: string
    prolemTemplate: string
    answerTemplate: string
    numberUnit: string
    totalUnit: string
}
export const ToSumUpProblemGenerator = () => {
    const result = {
        total: 0,
        unitKnown: 0,
        numberKnown: 0,
        unitNew: 0,
        numberNew: 0,
        problem: "",
        answer: "",
        prolemTemplate: "",
        answerTemplate: "",
        numberUnit: "",
        totalUnit: ""
    }
    const index = random(0,ToSumUpProblems.length-1)
    result.prolemTemplate = ToSumUpProblems[index].problem
    result.answerTemplate = ToSumUpProblems[index].answer
    result.total = random(ToSumUpProblems[index].magnitude/2,2*ToSumUpProblems[index].magnitude)
    result.total = 2*3*result.total
    const divisor = divisors(result.total)
    const unitKnownIndex = random(1,divisor.length-1)
    result.unitKnown = divisor[unitKnownIndex]
    result.numberKnown = Math.round(result.total/result.unitKnown)
    //
    divisor.splice(unitKnownIndex, 1); 
    result.unitNew = divisor[random(0,divisor.length-1)]
    result.numberNew = Math.round(result.total/result.unitNew)

    
    result.problem = result.prolemTemplate.replace('unitKnown',String(result.unitKnown))
    result.problem = result.problem.replace('numberKnown',String(result.numberKnown))
    result.problem = result.problem.replace('unitNew',String(result.unitNew))
    result.problem = result.problem.replace('numberNew',String(result.numberNew))

    result.answer = result.answerTemplate.replace('unitNew',String(result.unitNew))
    result.answer = result.answer.replace('numberNew',String(result.numberNew))
    
    return result
}